<template>
  <div class="address-group">
    <el-select class="address-input" v-model="province" placeholder="全部" :style="seletStyle">
      <el-option v-for="item in provinceList" :key="item.id" :label="item.areaName" :value="item.id" />
    </el-select>
    <el-select class="address-input" v-model="city" placeholder="全部" :style="seletStyle">
      <el-option v-for="item in cityList" :key="item.id" :label="item.areaName" :value="item.id" />
    </el-select>
    <el-select class="address-input" v-model="country" placeholder="全部" :style="seletStyle">
      <el-option v-for="item in countryList" :key="item.id" :label="item.areaName" :value="item.id" />
    </el-select>
  </div>
</template>

<script>
// 三级联动
import { areaProvince } from "../data/address";
import queryList from "utils/queryList";
let changePower = true;
export default {
  name: "userAddress",
  props:['seletStyle','defaultValue'],
  data() {
    return {
      provinceList: [],
      cityList: [],
      countryList: [],
      province: "",
      city: "",
      country: ""
    };
  },
  mounted() {
    this.handleProvince();
  },
  methods: {
    handleProvince() {
      //处理省份
      this.provinceList = areaProvince;
    },
    handleData(num, msg, callback) {
      //处理城市和地区
      if( !num ) return;
      queryList('area', {parentId: num}).then((res) => {
        if (res.data.code = this.ok) {
          if( callback ) callback(res.data.data);          
          if( msg === 'province' ) return this.cityList = res.data.data;
          return this.countryList = res.data.data;
        }
        this.$message('系统繁忙，请稍后再试')
      }).catch((err) => {
        console.log(err)
        this.$message('系统繁忙，请稍后再试!')
      })
    },
    getData(){
      let params = {
        province: this.province,
        city: this.city,
        country: this.country
      }
      return params;
    }
  },
  watch: {
    defaultValue(){
      if( this.defaultValue ) {
        const { province, city, country } = this.defaultValue;
        changePower = false;

        // 省份初始化
        if( !province ) return changePower = true;;
        let provinceArray = this.lodash.filter(this.provinceList, item=>{
          return item.areaName == province
        })
        this.province = provinceArray[0].id;

        // 城市初始化
        queryList('area', {parentId: this.province}).then((res) => {
          if (res.data.code = this.ok) {
            this.cityList = res.data.data;
            if( !city ) return changePower = true;
            let cityArray = this.lodash.filter(this.cityList, item=>{
              return item.areaName == city
            })
            this.city = cityArray[0].id; 

            //地区初始化
             queryList('area', {parentId: this.city}).then((res) => {
              if (res.data.code = this.ok) {
                this.countryList = res.data.data;
                if( !country ) return changePower = true;
                let countryArray = this.lodash.filter(this.countryList, item=>{
                  return item.areaName == country
                })
                this.country = countryArray[0].id;
                changePower = true; //打开切换开关
              }
            })
          }
        })
      }
    },
    province: function(curVal, oldVal) {
      if( !curVal || !changePower ) return;
      this.cityList = [];
      this.countryList = [];
      this.city = "";
      this.country = "";
      this.handleData(curVal,'province');
    },
    city: function(curVal, oldVal) {
      if( !curVal || !changePower ) return;
      this.countryList = [];
      this.country = "";
      this.handleData(curVal,'city');
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./../assets/less/color.less");
.address-group {
  display: flex;
  .address-input{
    margin-right: 20px;
    width: 210px;
    &.last-child{
      margin-right: 0;
    }
  }
}
</style>