<template>
  <div class="userDetail">
    <div>
      <h3>用户姓名：<span>{{userName}}</span></h3>
      <div class="list">
        <span>证件号码：</span>
        <span class="text">{{cardNum}}</span>
      </div>
      <div class="list">
        <span>手机号码：</span>
        <span class="text">{{mobile}}</span>
      </div>
      <div class="list">
        <span>用户地区：</span>
        <span class="text">{{address}}</span>
      </div>
    </div>
    <div>
      <h3>用户保单/服务单</h3>
      <el-table :data="userDetailList" align="center">
        <el-table-column prop="id" label="ID" />
        <el-table-column prop="coverName" label="投保人姓名" />
        <el-table-column prop="coverMobile" label="投保人电话" />
        <el-table-column prop="insuredName" label="被保人姓名" />
        <el-table-column prop="insuredMobile" label="被保人电话" />
        <el-table-column prop="good" label="所属产品" />
        <el-table-column prop="dateBegin" label="生效起期" />
        <el-table-column prop="dateEnd" label="生效至期" />
        <el-table-column prop="state" label="当前状态" />
        <el-table-column prop="server" label="绑定服务" />
        <el-table-column label="操作" width='100'>
          <template slot-scope="scope">
            <el-button @click="handleClick()" type="text" size="small">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div v-show="detailInfoEdit">
      <user-path-edit />
    </div>
    <div v-show="detailInfo">
      <user-path v-on:cancle="cancle" /> 
    </div>
  </div>
</template>

<script>
import { userDetailInfo } from "../../../../data/userManage";
import UserPath from './userDisPath';
import UserPathEdit from './userDisPathEdit';

export default {
  name: "userDetail",
  data() {
    return {
      userName: "龙先生",
      cardNum: "41254225541254",
      mobile: "15874521452",
      address: "北京市-北京市-朝阳区",
      detailInfo: false,
      detailInfoEdit: false,
      userDetailList: Array(2).fill(userDetailInfo)
    };
  },
  components: {
    UserPath,
    UserPathEdit
  },
  mounted(){
    if(this.$route.query.type === 'disPath'){
      this.detailInfo = false;
      this.detailInfoEdit = true;
    }
  },
  methods: {
    handleClick(){
      this.detailInfo = true;
      this.detailInfoEdit = false;
    },
    cancle(){
      if(this.$route.query.type !== 'disPath') return this.detailInfo = false;
      this.detailInfo = false;
      this.detailInfoEdit = true;
    }
  }
};
</script>
 
<style lang="less" scoped>
.userDetail {
  padding-left: 20px;
  .list {
    padding: 10px 0;
  }
}
</style>
 