<template>
  <div class="userDisPath">
    <h3 class="title">
      派单类型：
      <el-select v-model="type" class="com-input" placeholder="请选择派单类型">
        <el-option v-for="item in types" :key="item.value" :label="item.label" :value="item.value" />
      </el-select>
    </h3>
    <div class="info">
      <h3>服务预约信息</h3>
      <div class="list">
        <div class="left require"><span>预约手机号:</span></div>
        <el-input class="com-input" placeholder="请输入用户保单号" v-model="mobile" />
      </div>
      <div class="list">
        <div class="left require"><span>所患疾病:</span></div>
        <el-select v-model="codeType" class="com-input" placeholder="请选择">
          <el-option v-for="item in codeTypes" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
        <el-input class="com-input" placeholder="请输入所患疾病" v-model="code" />
      </div>
      <div class="list">
        <div class="left require"><span>所在地区:</span></div>
        <user-address seletStyle="width:220px"/>
      </div>
      <div class="list">
        <div class="left require"><span>期望就诊地区:</span></div>
        <user-address seletStyle="width:220px"/>
      </div>
      <div class="list">
        <div class="left require"><span>预约医院:</span></div>
        <el-select v-model="hospital" class="com-input" placeholder="请选择">
          <el-option v-for="item in hosList" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
        <el-input v-show="newHospital" class="com-input" placeholder="请输入医院" v-model="mobile" />
        <div class="create-hos blue" @click="creatHos">创建医院</div>
      </div>
      <div class="list">
        <div class="left require"><span>预约科室:</span></div>
        <el-select v-model="hopeCode" class="com-input" placeholder="请选择一级科室">
          <el-option v-for="item in codeTypes" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
        <el-select v-model="subHopeCode" class="com-input" placeholder="请选择二级科室">
          <el-option v-for="item in codeTypes" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
      </div>
      <div class="list">
        <div class="left">指定医生:</div>
        <el-input class="com-input" placeholder="请输入指定医生" v-model="doctor" />
      </div>
      <div class="list hos-state">
        <div class="left require"><span>住院状态:</span></div>
        <el-select v-model="liveHos" class="com-input" placeholder="请选择">
          <el-option v-for="item in liveHosState" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
        <div class="right" v-show="liveHosMore">
          <div class="item">
            <span>住院时间：</span>
            <el-date-picker v-model="dateBegin" type="date" placeholder="选择日期" />
          </div>
          <div class="item">
            <span>已交费用：</span>
            <el-input class="com-input" placeholder="请输入已交费用" v-model="cost" />
          </div>
          <div class="item">
            <span>催缴费用：</span>
            <el-input class="com-input" placeholder="请输入催缴费用" v-model="newCost" />
          </div>
        </div>
      </div>
      <div class="list">
        <div class="left require"><span>社保状态:</span></div>
        <el-select v-model="security" class="com-input" placeholder="请选择">
          <el-option v-for="item in securityType" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
      </div>
      <div class="list">
        <div class="left">社保所在地区:</div>
        <user-address seletStyle="width:220px"/>
      </div>
      <div class="list">
        <div class="left">本次社保直结:</div>
        <el-radio v-model="securityRes" label="1">是</el-radio>
        <el-radio v-model="securityRes" label="2">否</el-radio>
      </div>
      <div class="list">
        <div class="left">新农合盖章:</div>
        <el-radio v-model="stamp" label="1">是</el-radio>
        <el-radio v-model="stamp" label="2">否</el-radio>
      </div>
      <div class="list">
        <div class="left">用户备注:</div>
        <el-input type="textarea" :rows="3" placeholder="请输入内容" v-model="textarea" />
      </div>
      <div class="list">
        <div class="left"></div>
        <div>
          <el-button>提交</el-button>
          <el-button @click="cancle">取消</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserAddress from "../../../../components/address";
export default {
  name: "userDisPath",
  data() {
    return {
      type: "",
      mobile: "",
      code: "",
      codeType: "",
      hospital: "",
      hopeCode: "",
      subHopeCode: "",
      doctor:"",
      security:"",
      dateBegin:"",
      cost:"",
      newCost:"",
      liveHos:"",
      textarea:"",
      securityRes:"",
      stamp:"",
      liveHosMore:false,
      newHospital: false,
      types: [
        {
          value: "A",
          label: "押金垫付"
        },
        {
          value: "B",
          label: "网银"
        }
      ],
      codeTypes: [
        {
          value: "A",
          label: "呼吸科"
        },
        {
          value: "B",
          label: "消化科"
        }
      ],
      hosList: [
        {
          value: "A",
          label: "北京儿童医院"
        },
        {
          value: "B",
          label: "北京解放军医院"
        }
      ],
      liveHosState:[
        {
          value: "A",
          label: "已住院"
        },
        {
          value: "B",
          label: "未住院"
        }
      ],
      securityType:[
        {
          value: "A",
          label: "已缴纳"
        },
        {
          value: "B",
          label: "未缴纳"
        }
      ]
    };
  },
  components: {
    UserAddress
  },
  methods: {
    cancle() {
      this.$router.back(-1);
    },
    creatHos() {
      this.newHospital = true;
    }
  },
  watch: {
    liveHos: function(curVal, oldVal) {
      if (!curVal) return;
      if ( curVal === 'A' ) return this.liveHosMore = true;
      this.liveHosMore = false;
    }
  }
};
</script>
 
<style lang="less" scoped>
.userDisPath {
  h3 {
    padding-top: 10px;
    .el-select {
      margin-left: 10px;
    }
  }
  .el-select {
    margin-right: 20px;
  }
  .el-input {
    width: 220px;
    margin-right: 20px;
  }
  .el-radio{
    height: 40px;
    line-height: 40px;
  }
  .el-textarea{
    width: 460px;
    height: inherit !important;
    line-height: inherit !important;
  }
  .info {
    h3 {
      padding: 0;
      margin: 0;
      padding-bottom: 10px;
      font-size: 16px;
    }
    .list {
      padding: 5px 0;
      display: flex;
      div {
        height: 40px;
        line-height: 40px;
      }
      .left {
        min-width: 100px;
        text-align: right;
        margin-right: 10px;
        &.require{
          span{
            position: relative;
            display: inline-block;
            &:after{
              position: absolute;
              content: '*';
              color: red;
              left: -10px;
              top: 0;
            }
          }
        }
      }
      .right{
        height: inherit;
        line-height: inherit;
        .item{
          height: 40px;
          line-height: 40px;
          margin-bottom: 10px;
        }
      }
      .blue {
        color: #068ff3;
        cursor: pointer;
      }
    }
  }
}
</style>
 